<template>
  <div v-bind:class="['header', theme]">
    <div class="logo" @click="logoClick">z0ey</div>
    <div class="menu"><i class="fa fa-eye" @click="toggleTheme"></i></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Events from '@/events'

export default {
  name: 'header-bar',

  computed: {
    ...mapGetters([
      'theme'
    ])
  },

  methods: {
    logoClick () {
      Events.$emit('logoClick')
    },

    toggleTheme () {
      this.$store.commit('ToggleTheme', {})
    }
  }
}
</script>

<style lang="stylus">
@import '~@/theme.styl'

.header
  width 100%
  height 50px
  position fixed
  display flex
  flex 1
  justify-content center
  background-color $theme-default-bg
  border-bottom 1px solid $theme-default-border

  &.theme-dark
    border-bottom 1px solid $theme-dark-border

  &.theme-dark
    background-color $theme-dark-bg

  .logo
    cursor pointer
    font-family 'Sacramento', 'Damion', cursive
    font-size 2em

    &:hover
      text-shadow 1px 1px

  .menu
    margin-right 1em
    line-height 50px
    height 50px
    position absolute
    right 0
    top 0
    cursor pointer
</style>



